.skills-main-div {
	display: flex;
}
.skills-text-div {
	margin-left: 50px;
}

.skills-main-div > * {
	flex: 1;
	margin-bottom: 30px;
}

.skills-image-div > img {
	max-width: 100%;
	height: auto;
}

.skills-heading {
	font-size: 30px;
	font-weight: 400;
	line-height: normal;
}

/* Media Query */
@media (max-width: 1380px) {
	.skills-heading {
		font-size: 25px;
	}
}
@media (max-width: 768px) {
	.skills-heading {
		font-size: 20px;
		text-align: center;
	}
	.greeting-text-p {
		font-size: 12px;
		line-height: normal;
	}
	.skills-main-div {
		flex-direction: column;
	}
	.skills-text-div {
		margin-left: 0px;
		margin: 20px;
	}
	.skills-text {
		font-size: 16px;
	}
	.skills-text-subtitle {
		font-size: 16px;
		text-align: center;
	}
	.skills-image-div {
		/* display: none; */
		order: 2;
	}
}
